import React, { Component } from 'react';
import SimpleSlider from '../../components/Slider';
import './services.css'
import CLIENTEXDATA from './../../data/ClientExData';

class Services extends Component {
    
    render() { 
        return (
            <div className='services'>
            <div className='services-hero'>
                <span></span>
                <img src='/assets/images/services-hero.jpg'/>
                <article>
                    <h1>
                        Our services
                    </h1>
                </article>
            </div>

            <div className='content'>
                <div className='buy'>
                    <h1>Buy or sell a land</h1>
                </div>
                <div className='buy-container'>
                    <div className='buy-pic'>
                        <img 
                            src='/assets/images/buy-pic.jpg'
                        />
                    </div>
                    <div className='buy-details'>
                        <h3>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec sed 
                        semper lacus. Morbi consectetur quam nec lorem hendrerit condimentum. 
                        Aliquam vulputate tristique tincidunt. Nulla suscipit eros non purus pharetra 
                        consectetur. Phasellus vulputate et enim nec vestibulum. Vivamus accumsan lobortis ultrices. 
                        Quisque vitalacus. Morbi consectetur quam nec lorem hendrerit condimentum. 
                        Aliquam vulp</h3>
                    </div>
                </div>
                <div className='develop'>
                    <h1>Develop your land</h1>
                </div>
                <div className='develop-container'>
                    <div className='develop-details'>
                        <h3>Just a piece of land is good enough but to really understand its true
                         value, you need to groom it a little. We undertake property developments through 
                         which we will develop your properties to gain the maximum advantage of the land, in 
                         accordance with the laws of the state and in line with your wishes</h3>
                    </div>
                    <div className='develop-pic'>
                        <img 
                            src='/assets/images/develop.jpg'
                        />
                    </div>
                </div>
                <div className='services-ex'>
                    <h1>Client expeerience</h1>
                    <SimpleSlider data={CLIENTEXDATA} className='clientEx__slider' />
                </div>
                <div className='consulting'>
                    <h1>Advisory and consultency</h1>
                    <div className='consulting-container'>
                        <div className='consulting-pic'>
                            <img 
                                src='/assets/images/consulting.jpg'
                            />
                        </div>
                        <div className='consulting-details'>
                        <h3>Confused? Speak to us about all your real-estate related issues. We provide consultant 
                        aid on real estate buying and selling, developments, property management and networking with 
                        professionals.</h3>
                        </div>
                    </div>
                </div>
            </div>
            </div>
        );
    }
}
 
export default Services;